<template>
    <div>
        <h4>""</h4>
        <form id="form-product" class="form-horizontal">
          <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label" for="lname"></label>
            </div>
            <div class="col-sm-5">
                <input type="text" class="form-control"  v-model="CountryName">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div>
        <!-- <div class="form-group">
            <div class="col-sm-2">
                <label class="control-label"></label>
            </div>
            <div class="col-sm-5">
                    <input type="text" class="form-control" readonly v-model="point">
            </div>
            <div class="col-sm-5 help-block"></div>
        </div> -->
        <!-- <div class="form-group">
              <div id="map" ref="map"></div>
        </div> -->
        <div class="form-group">
          <div class="col-sm-10 col-sm-offset-2">
            <input type="button" value="" class="btn btn-success" @click="formSubmit">
          </div>
        </div>

      </form>
    </div>
</template>
<script>
import uploader from './common/upload'
export default {
  components:{
      uploader
  },  
  name: "food",
  data () {
    return {
        list:'',
        CountryName:'',
        point:''
    };
  },
  methods:{
    getFood(){
          this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                        this.list=response.data;
            })   
    },
    getUploadInfo(img){
        this.imgInfo=img;
        console.log(this.imgInfo);
    },
    formSubmit(){
        console.log(this.rangerVal);
            this.$http.post('/ZooCMS/animalinfo/setHome',{country:this.CountryName,name:this.name},{emulateJSON: true}).then(function(response){
                        console.log(response.data);
                       // this.list=response.data;
            })     
        //  this.$http.post('/ZooCMS/animalinfo/getFood',{name:this.$route.query.name},{emulateJSON: true}).then(function(response){
        //                 console.log(response.data);
        //                 this.list=response.data;
        // })     
    },
    getMap(){
            this.map = new BMap.Map(this.$refs.map);
            this.map.centerAndZoom(new BMap.Point(116.404, 39.915),1);// ,
            this.map.addControl(new BMap.MapTypeControl({//
            mapTypes:[  
                BMAP_NORMAL_MAP,
                BMAP_HYBRID_MAP
                ]}));
            this.map.setCurrentCity("");//  
            this.map.enableScrollWheelZoom(true);//
            this.localSearch = new BMap.s(this.map);
            this.localSearch.enableAutoViewport(); //
    },
    searchByStationName() {
            this.map.clearOverlays();//
        let keyword = this.CountryName;
        this.localSearch.setSearchCompleteCallback((searchResult)=> {
        let poi = searchResult.getPoi(0);
               console.log(poi);
        this.point = poi.point.lng + "," + poi.point.lat; //
        this.map.centerAndZoom(poi.point,4);
                let marker = new BMap.Marker(new BMap.Point(poi.point.lng, poi.point.lat));  // 
                this.map.addOverlay(marker);
        });
        this.localSearch.search(keyword);
    }
  },
  mounted(){
      this.getFood();
      //this.id=this.$route.query.id;
      this.name=this.$route.query.name;
      //this.getMap();
  }
}
</script>
<style lang="css" scoped>
    table tr td img{
         width:40px;
    }
    #map{
        width:100%;
        height:300px;
    }
</style>


